body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: rgb(170, 83, 83);
  height: 70px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}





.imageDisplay {
    display: inline-block;
    flex-wrap: wrap;
}

.textLayout {
    width:189px;
    height:175px;
    border-style:solid;
    border-color:#336699;
    word-wrap: break-word;
    margin: 20px;
    letter-spacing: 2px;
    text-align: center;
    line-height: 1.8;
    background-color: rgb(126, 67, 67);
    font-family: fantasy;
    font-weight: bold;
}

img {
  width: 195px;
  height: 195px;
  margin: 20px;
}

.likeButton:hover {
    border-radius: 4px;
    background: white;
    background-color: rgb(27, 52, 63);
}
